import axios from "axios";
import { endpoints } from "Constants/api";
import axiosHelpers from "Constants/axiosHelpers";

export const getFrequencies = async () => {
    const request = await axios({
        method: "get",
        url: endpoints.serviceAmenity.GET_FREQUENCY_LIST,
        headers: {
            // Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
    }).catch(axiosHelpers);
    return request;
}

export const addFrequency = async (data) => {
    const request = await axios({
        method: "post",
        url: endpoints.serviceAmenity.ADD_FREQUENCY,
        headers: {
            // Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
        data
    }).catch(axiosHelpers);
    return request;
}

export const listServiceAmenity = async (params) => {
    const request = await axios({
        method: "get",
        url: endpoints.serviceAmenity.SERVICES_AMENITIES,
        headers: {
            // Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
        params
    }).catch(axiosHelpers);
    return request; 
}

export const addServicesAmenities = async (data, isEdit) => {
    const request = await axios({
        method: !isEdit ? "post" : "put",
        url: endpoints.serviceAmenity.SERVICES_AMENITIES,
        headers: {
            // Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
        data
    }).catch(axiosHelpers);
    return request; 
}

export const deleteServicesAmenities = async (params) => {
    const request = await axios({
        method: "post",
        url: endpoints.serviceAmenity.SERVICES_AMENITIES,
        headers: {
            // Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
        params
    }).catch(axiosHelpers);
    return request; 
}

export const getRoomType = async (propertyId) => {
    const request = await axios({
        method: "get",
        url: endpoints.serviceAmenity.GET_ROOM_TYPE + `/${propertyId}`,
        headers: {
            // Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
    }).catch(axiosHelpers);
    return request; 
}